body {
    background-image: url(../images/bg-top.svg), url(../images/bg-bottom.svg);
    background-repeat: no-repeat;
    background-position: right top,left bottom;
    background-size: 250px;
    background-color: hsl(240, 78%, 98%);
}
.ourPrice, .annual, .month, .heading, .heading2,.price, .price2, .sto, .users, .data, .sto2, .user2,
 .data2, .btn, .btn2 {
font-family: 'Montserrat', sans-serif;
 }

.ourPrice {
        color: hsl(232, 13%, 33%); 
        font-weight: 800;
        text-align: center;
        margin-top: 30px;
        Font-size: 25px;
    
    }
 .para {
     margin-top: 30px;
     color: hsl(234, 14%, 74%);
     display: flex;
     width: 200px;
     margin: 30px auto;
     }
.annual, .month {
    margin-right: 10px;
    font-weight: 600;
     }

.container {
    display: flex;
    justify-content: center;
    box-sizing: border-box;
}
.basic, .master {
    width: 26.5%;
    border-radius: 5px;
    text-align: center;
    height: 30rem;
    margin-top: 1rem;
    
}

#professional {
    height: 32rem;
    background-color: hsl(236, 72%, 79%);
    border-radius: 5px;
    width: 26.5%;
    text-align: center;
}
.prof {
    margin-top: 32px;
}
.basic, .master {
    background-color: white;
}
.heading {
    font-size: 15px;
    
    font-weight: 600;
    color: hsl(232, 13%, 33%);
}
.heading2 {
font-size: 15px;
    
    font-weight: 600;
    color: white;
}
.price {
    color: hsl(232, 13%, 33%);
    border-bottom: 1px solid hsl(234, 14%, 74%);
}
.price, .price2 {
font-size: 40px;
    font-weight: 800;
    padding-bottom: 30px;
    line-height: 10vh;
    margin: 13px 20px 0px 20px;
}
.price2{
        color: white; 
        border-bottom: 1px solid hsl(237, 63%, 64%);
}
.sto, .users, .data {
    border-bottom: 1px solid hsl(234, 14%, 74%);
     line-height: 6vh;
     padding-bottom: 20px;
     margin: 13px 20px 0px 20px;
     font-size: 15px;
     font-weight: 600;
     color: hsl(232, 13%, 33%);
}
 .sto2, .user2, .data2 {
    border-bottom: 1px solid hsl(237, 63%, 64%);
        line-height: 6vh;
        padding-bottom: 20px;
        margin: 13px 20px 0px 20px;
        color: white;
        font-size: 15px;
      font-weight: 600;
 }
   .btn {
     background-color: hsl(237, 63%, 64%);
     color: white;
 } 
 .btn, .btn2 {
    border-radius: 8px;
    border-style: none;
    font-size: 10px;
    cursor: pointer;
    font-weight: 700;
    padding: 8px 40px;
    margin: 20px;
 }
   .btn2 {
     background-color: white;
     color: hsl(237, 63%, 64%);
 }  


